input[type='”file”']#archivo {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
  display: none !important;
}

label[for='archivo'] {
  font-size: 12px;
  font-weight: 600;
  color: #fff;
  background-color: #099ef5;
  display: inline-block;
  transition: all 0.5s;
  cursor: default;
  padding: 6px 15px !important;
  text-transform: uppercase;
  width: 100%;
  text-align: center;
  margin-top: 2px;
}
